.navbar {
    height: 56px;
    background-color: #151515;
    display: -ms-flexbox;
    display: flex;
}

.navbar .nav-link {
    -ms-flex: 1 1;
        flex: 1 1;
}

.navbar .nav-link button:hover {
    cursor: pointer;
}

.navbar .nav-link button {
    text-align: center;
    color: rgba(244,244,244,.7);
    font-size: 1.4rem;
    text-decoration: none;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    width: 100%;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    font-family: Audiowide,sans-serif;
    border: none;
    background: none;
    outline: none;
}

.navbar .nav-link.active button {
    color: #5EC0D5;
}

.navbar .nav-link.active button:hover {
    cursor: auto;
}


